@import url(https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rubik:ital@1&display=swap);
/* 기본 CSS 값 */

html {
  width: 100%;
}

body {
  background-color: #f2eeea;
  font-family: "Nanum Gothic", sans-serif;
  text-align: center;
}

a {
  text-decoration: none;
  color: inherit;
}

h2 {
  color: #0e0a07;
  margin-top: 60px;
  font-family: "Roboto", sans-serif;
}

input {
  border: 0px;
  background: #fdfcfa;
}

button,
textarea,
input {
  -webkit-appearance: none;
  -webkit-border-radius: 0;
}

/* 전체 레이아웃 관련 */

nav {
  padding-top: 60px;
}

.list {
  border-left: 1px solid #fcfbf7;
  padding-top: 60px;
}

article {
  background-color: #fdfcfa;
  padding-top: 60px;
  border-radius: 15px;
}

footer {
  display: inline-block;
  background-color: white;
  border-radius: 15px;
  padding: 10px;
  text-align: center;
}

.menuBTN {
  display: none;
  position: absolute;
  right: 30px;
  top: 32px;
}

.box {
  display: grid;
  grid-template-columns: 220px 1fr 1fr;
  background-color: #ffffff;
  margin-left: 1%;
  margin-top: 25px;
  margin-bottom: 25px;
  border-radius: 15px;
  width: 98%;
  box-shadow: 1px 1px 3px #f0ede9;
  color: #050302;
  text-align: center;
}

.ulMenu {
  list-style: none;
  padding-left: 0px;
}

/* 미디어 쿼리 */

@media (max-width: 1024px) {
  .box {
    grid-template-columns: 220px 1fr;
    grid-auto-rows: auto, auto;
  }

  .box article {
    grid-column: 2;
    grid-row: 2;
  }

  /* 노티바 부분 숨기기 */
  .noti_search {
    display: none;
  }

  .noti_search_n {
    display: none;
  }

  .search {
    display: none;
  }

  .icon2 {
    display: none;
  }
}

@media (max-width: 800px) {
  .box {
    display: block;
    grid-template-columns: 1fr;
    width: 100%;
    margin-left: 0;
    margin-top: 0;
    margin-bottom: 0;
  }

  .menuBTN {
    display: block;
  }

  nav {
    padding-top: 15px;
    margin-bottom: 15px;
  }

  article {
    border-left: 0px;
  }

  footer {
    margin-top: 15px;
  }

  .list {
    border-top: 1px solid #f5f5f5;
    padding-top: 20px;
  }

  .navBar {
    display: none;
  }

  /* 노티바 부분 숨기기 */
  .noti_search {
    display: none;
  }

  .noti_search_n {
    display: none;
  }

  .search {
    display: none;
  }

  .icon2 {
    display: none;
  }
}

/* 로고(+ClassName) 및 카피라이트 */
.logo {
  display: inline-block;
  font-size: 1.5rem;
  font-weight: bolder;
}

.logo:hover {
  cursor: pointer;
}

.logoWork {
  color: red;
}

.className {
  font-weight: bold;
  font-size: 0.8rem;
}

.className:hover {
  cursor: pointer;
}

.copyright {
  text-align: center;
  font-size: 0.5rem;
}

/* 유저 정보 */

.userInfo {
  display: block;
  background-color: #fcfbf7;
  border-radius: 25px;
  margin-top: 40px;
  margin-left: 5%;
  padding-top: 5px;
  width: 90%;
  height: 48px;
}

.userInfo_name {
  font-weight: bold;
  font-size: 12px;
}

.userInfo_info {
  color: gray;
  font-size: 10px;
}

.signOut {
  display: inline-block;
  margin-left: 60%;
  margin-top: 5px;
  color: gray;
  font-size: 10px;
  background-color: #fcfbf7;
  padding: 4px;
  padding-left: 8px;
  padding-right: 8px;
  border-radius: 10px;
}

.signOut:hover {
  cursor: pointer;
}

/* 메뉴 관련 레이아웃 */

.menuForm {
  margin-top: 40px;
  font-size: 15px;
  text-align: left;
  padding-left: 50px;
  font-family: "Roboto", sans-serif;
  color: #cac9cd;
}

.menu {
  margin-bottom: 40px;
}

.menu:hover {
  cursor: pointer;
  color: #3f3e3e !important;
}

.menu_open {
  margin-bottom: 40px;
  color: #3f3e3e !important;
}

.menu_open:hover {
  cursor: pointer;
}

/* 레이아웃 */

.articleForm {
  /* padding-top: 20px; */
  padding-bottom: 20px;
  text-align: left;
  margin-left: 5%;
}

/* 알림과 검색창 */
.noti_search {
  display: grid;
  grid-template-columns: 4fr 6fr;
  text-align: left;
  padding-bottom: 20px;
}

.noti_search_n {
  padding-top: 17px;
}

.noti_search_s {
  display: grid;
  grid-template-columns: 60px 1fr;
}

.icon {
  border-radius: 30px;
  background-color: white;
  padding: 10px;
  padding-top: 17px;
  margin-left: 20px;
  box-shadow: 1px 1px 4px #eeedeb;
}

.icon:hover {
  cursor: pointer;
}

.icon2 {
  border-radius: 30px;
  background-color: white;
  padding: 10px;
  padding-top: 17px;
  margin-left: 20px;
  box-shadow: 1px 1px 4px #eeedeb;
}

.icon2:hover {
  cursor: pointer;
}

.search {
  height: 30px;
  padding-top: 15px;
  padding-left: 15px;
}

/* 지난 수업 */
.lastClass {
  display: grid;
  grid-template-columns: 120px 1fr;
  border-radius: 15px;
  background-color: white;
  padding: 10px;
  text-align: center;
  box-shadow: 1px 1px 4px #eeedeb;
  width: 90%;
}

.lastClass:hover {
  cursor: pointer;
  border: 1px solid gray;
}

/* 다음 수업 */
.nextClass {
  display: grid;
  grid-template-columns: 1fr 120px;
  border-radius: 15px;
  background-color: white;
  padding: 15px;
  text-align: center;
  box-shadow: 1px 1px 4px #eeedeb;
  width: 90%;
}

.nextClass:hover {
  cursor: pointer;
  border: 1px solid gray;
}

/* 수업 타이틀, 내용 */
.class_title {
  display: block;
  margin-bottom: 10px;
  font-weight: bold;
  font-size: 1.5rem;
}

.class_contents {
  display: block;
  color: #c1c4c5;
}

/* 숙제 내용 제목과 날짜 */
.homeworkTitleDate {
  display: grid;
  grid-template-columns: 1fr 100px;
  font-size: 12px;
}

.homeworkContentTitle {
  color: #0e0a07;
  font-family: "Roboto", sans-serif;
  font-size: 25px;
  font-weight: bold;
}

.homeworkDate {
  margin-top: 12px;
  color: #b7b9bd;
}

/* 숙제 메뉴 컨텐츠 */
.homeworkContents {
  display: block;
  width: 90%;
  margin-left: 1%;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 4%;
  margin-bottom: 15px;
  margin-top: 15px;
  border-radius: 15px;
  background-color: white;
  box-shadow: 1px 1px 4px #eeedeb;
}

/* 숙제 제출 버튼 */
.homeworkSubmit {
  display: grid;
  grid-template-columns: 80px 1fr;
  background-color: white;
  border-radius: 15px;
  width: 90%;
  padding-left: 5%;
  box-shadow: 1px 1px 4px #eeedeb;
}

.homeworkSubmit:hover {
  cursor: pointer;
  border: 1px solid gray;
}

.homeworkSubmitL {
  margin-top: 30px;
  height: 80px;
  text-align: center;
}

.homeworkSubmitR {
  margin-top: 35px;
  font-size: 30px;
  font-weight: bold;
  text-align: center;
}

/* 게시글 리플 폼 */
.replyForm {
  display: grid;
  grid-template-columns: 80px 1fr;
  width: 90%;
  margin-left: 1%;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 4%;
  margin-bottom: 15px;
  margin-top: 15px;
  border-radius: 15px;
  background-color: white;
  box-shadow: 1px 1px 4px #eeedeb;
}

.replyFormL {
  text-align: center;
}

.replyFormR {
  padding-left: 10px;
}

.replyImgStyle {
  -webkit-transform: scaleX(-1);
          transform: scaleX(-1);
}

/* 작성자 이름 */
.name {
  text-align: right;
  color: #b7b9bd;
  font-size: 12px;
  padding-right: 15px;
}

/* 게시판 폼 */
.boardForm {
  margin-top: 25px;
  padding: 10px;
  border-radius: 15px;
  display: grid;
  grid-template-columns: 1fr 6fr 2fr 1fr;
  background-color: #fbfaf6;
  font-size: 1rem;
}

.boardForm:hover {
  cursor: pointer;
  border: 1px solid gray;
}

/* 기본 레이아웃 */

.listForm {
  width: 90%;
  margin-left: 5%;
  text-align: left;
  padding-bottom: 20px;
}

/* 현재 페이지 안내 */

.currMenu {
  font-size: 3.5rem;
  font-weight: bold;
  font-family: "Roboto", sans-serif;
  margin-bottom: 5px;
}

.listClassName {
  font-size: 1.2rem;
  font-weight: bold;
  color: #adb0b5;
  margin-bottom: 40px;
}

/* 선생님 정보 */
/* 레이아웃 */
.teacherForm {
  display: grid;
  grid-template-columns: 3fr 7fr;
  border-radius: 15px;
  padding: 2%;
  margin-top: 20px;
  background-color: #e9f4fe;
  width: 95%;
  font-weight: bolder;
  text-align: center;
  color: red;
  height: 150px;
}

.teacherInfo {
  text-align: left;
  font-size: 9px;
  padding-left: 15px;
  padding-top: 5px;
}

.teacherJPG {
  border-radius: 15px;
}

/* 세부 내용 박스 */
.teacherTag {
  display: inline-block;
  border-radius: 5px;
  background-color: white;
  padding: 5px;
  margin-bottom: 2px;
}

.teacherTag2 {
  display: inline-block;
  border-radius: 5px;
  background-color: white;
  padding: 5px;
  margin-bottom: 2px;
  color: #87b4ec;
}

.teacherTag2:hover {
  cursor: pointer;
}

.teacherContent {
  display: block;
  color: #1d5bcc;
  font-size: 15px;
  padding-left: 2px;
  padding-top: 2px;
}

/* 간단 공지 */
.nnhForm {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.notiForm {
  width: 95%;
  padding: 5px;
  margin-right: 20px;
}

.noticeTitle {
  padding-bottom: 5px;
  color: black;
  font-size: 0.8rem;
  font-weight: bolder;
}

.noticeContent {
  padding-top: 5px;
  margin-bottom: 40px;
  color: #a3a5a8;
  font-size: 0.7rem;
}

.notiDate {
  padding-left: 3px;
  color: #b7b9bd;
  font-size: 0.4rem;
  font-weight: 100;
}

/* 숙제 */

.homework {
  display: grid;
  grid-template-rows: 1fr 1fr;
  color: white;
  font-size: 1rem;
  text-align: center;
  font-weight: bold;
}

.homeworkColumn1 {
  padding: 5px;
  padding-top: 40px;
  margin: 5px;
  background-color: #273598;
  border-radius: 15px;
  box-shadow: 1px 1px 3px #f0ede9;
  height: 80px;
}

.homeworkColumn1:hover {
  cursor: pointer;
  background-color: #5b6297;
}

.homeworkColumn2 {
  padding: 5px;
  padding-top: 40px;
  margin: 5px;
  background-color: #ee815a;
  border-radius: 12px;
  box-shadow: 1px 1px 3px #f0ede9;
  height: 80px;
}

.homeworkColumn2:hover {
  cursor: pointer;
  background-color: #e6a189;
}

.hwDate {
  padding-left: 3px;
  color: #a3a5a8;
  font-size: 0.5rem;
  background-color: white;
  opacity: 0.7;
  border-radius: 5px;
  padding: 3px;
}

/* 달력 */
.calender {
  width: 95%;
  margin-top: 15px;
  padding: 5px;
  padding-left: 15px;
  border-radius: 15px;
  border: 1px solid #f8f8f8;
  text-align: center;
}

/* 숙제 리스트 */
.homeworkListForm {
  display: grid;
  margin-top: 30px;
  padding: 10px;
  grid-template-columns: 150px 1fr;
  border-radius: 15px;
  width: 95%;
  background-color: #f5effe;
}
.homeworkListForm:hover {
  cursor: pointer;
  border: 1px solid gray;
}

.homeworkListForm2 {
  display: grid;
  margin-top: 30px;
  padding: 10px;
  grid-template-columns: 150px 1fr;
  border-radius: 15px;
  width: 95%;
  background-color: #ffe88a;
}
.homeworkListForm2:hover {
  cursor: pointer;
  border: 1px solid gray;
}

/* 숙제 리스트 오른쪽 부분 */

.homeworkListForm_r {
  color: #10288f;
  margin-left: 20px;
  margin-top: 10px;
}

.homeworkListForm_r2 {
  color: #806600;
  margin-left: 20px;
  margin-top: 10px;
}

.homeworkListDate {
  width: 150px;
  height: 100px;
  border-radius: 15px;
  background-color: white;
  font-weight: bold;
  text-align: center;
  padding-top: 50px;
  font-size: 35px;
  font-family: "Rubik", sans-serif;
}

.homeworkListTag {
  display: inline-block;
  border-radius: 5px;
  background-color: white;
  padding: 5px;
  margin-bottom: 2px;
  color: red;
  font-size: 12px;
  margin-bottom: 15px;
}

.homeworkListTitle {
  display: block;
  margin-bottom: 5px;
  margin-left: 3px;
  font-weight: bold;
  font-size: 20px;
}

.homeworkListMTag {
  padding-top: 15px;
  font-size: 12px;
  color: #3e4c88;
}

.homeworkListMTag2 {
  padding-top: 15px;
  font-size: 12px;
  color: #7c6f39;
}

.logoForm {
  margin: 30px;
}

.login {
  position: absolute;
  width: 300px;
  height: 450px;
  padding: 30px, 20px;
  background-color: #ffffff;
  text-align: center;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border-radius: 15px;
  box-shadow: 0px 0px 3px #f0ede9;
  color: #050302;
}

.idForm,
.pwForm {
  border-bottom: 2px solid #adadad;
  margin: 30px;
  padding: 10px 10px;
}

.id,
.pw {
  width: 100%;
  height: 100%;
  border: none;
  outline: none;
  font-size: 18px;
  background: white;
}

.loginButton {
  padding-top: 20px;
}

.btn {
  position: relative;
  left: 40%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  margin-bottom: 20px;
  width: 80%;
  height: 40px;
  background: #ee815a;
  background-position: left;
  background-size: 200%;
  color: white;
  font-weight: bold;
  border: none;
  cursor: pointer;
  transition: 0.4s;
  display: inline;
  font-size: 13px;
  border-radius: 10px;
}

.btn:hover {
  background-position: right;
  background-color: #e9a58c;
}

.joinAccount {
  display: block;
  width: 80%;
  margin-left: 10%;
  border-radius: 10px;
  color: white;
  font-weight: bolder;
  padding-top: 12px;
  padding-bottom: 12px;
  background: #ee815a;
  font-size: 12px;
}

.joinAccount:hover {
  cursor: pointer;
  background-position: right;
  background-color: #e9a58c;
}

.google {
  padding: 5px;
}

.googleBTN {
  background-color: #c7562d;
  border: 0px;
  border-radius: 15px;
  color: white;
  padding: 5px;
  padding-left: 10px;
  padding-right: 10px;
  margin-top: 15px;
}

.googleBTN:hover {
  cursor: pointer;
  background-color: #da8567;
}

